import React, { useState } from 'react';
import './BoardContainer.css';


const rows = [0, 1];
const totalSections = 24;
const state = {
    hasStarted: false,
    pieces: [],
    totalSections: 24
};

const player1Spaces = [24, 13, 8, 6];
const player2Spaces = [1, 12, 17, 19];

const placementPlayer1 = {
    24: 2,
    13: 5,
    8: 3,
    6: 5
};

const placementPlayer2 = {
    1: 2,
    12: 5,
    17: 3,
    19: 5
};

const random = (num) => {
    return Math.floor(Math.random()*num)
};

const getRandomStyles = () => {
    let r = random(255);
    let g = random(255);
    let b = random(255);
    let mt = random(200);
    let ml = random(50);
    let dur = random(5)+5;
    return `
  background-color: rgba(${r},${g},${b},0.7);
  color: rgba(${r},${g},${b},0.7); 
  box-shadow: inset -7px -3px 10px rgba(${r-10},${g-10},${b-10},0.7);
  margin: ${mt}px 0 0 ${ml}px;
  animation: float ${dur}s ease-in infinite
  `
};


const sayHappyBirthday = () => {
    let balloonContainer = document.getElementById("balloon-container");
    for (let i = 200; i > 0; i--) {
        let balloon = document.createElement("div");
        balloon.className = "balloon";
        balloon.style.cssText = getRandomStyles();
        balloonContainer.append(balloon);
    }
}

const startGame = (row, index) => {
    let totalPieces = 0;
    let pieces = [], className = "Piece Piece-1";

    if (player1Spaces.includes(index)) {
        totalPieces = placementPlayer1[index];
    } else if (player2Spaces.includes(index)) {
        totalPieces = placementPlayer2[index];
        className = "Piece Piece-2"
    }

    if (totalPieces > 0) {
        let i = 0;
        while (i < totalPieces) {
            pieces.push(<span className={className}> </span>);
            i++;
        }
    }
    return pieces;
}

const createSpace = (total = 24, row = 0) => {
    let totalSpaces = [];
    let starting = total, ending = 1;

    if (row) {
      starting = (total / 2);
      ending = 1;
    } else {
      ending = (total / 2 + 1);
    }


    while (starting >= ending) {
        if (!row) {
            totalSpaces.push(<div id={starting} key={starting} className="BoardSpace arrow-down">{startGame(row, starting)}</div>)
        } else {
            totalSpaces.push(<div id={starting} key={starting} className="BoardSpace arrow-up">{startGame(row, starting)}</div>)
        }

        starting--;
    }
    return totalSpaces
};

function BoardContainer() {
    return (
        <div className="BoardContainer">
            <div className="Board">
                <div className="Board-Row1">
                    <div className="Board-Col1">

                    </div>
                    {createSpace(24, 0)}
                    <div className="Board-Col2">

                    </div>
                </div>
                <div className="Board-Row2">
                    <div className="Board-Col1">

                    </div>
                    {createSpace(24, 1)}
                    <div className="Board-Col2">

                    </div>
                </div>
            </div>
            <button onClick={sayHappyBirthday} className="GameButton">
                <img src="https://upload.wikimedia.org/wikipedia/sco/thumb/1/16/Baltimore_Ravens_logo.svg/1200px-Baltimore_Ravens_logo.svg.png"></img>
            </button>
        </div>
    );
}

export default BoardContainer;

